  height: 40px;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.scrollToTop-svg {
  width: 35px;
  height: 35px;
  fill: var(--accent-color);
  stroke: var(--accent-color);
}

:root {
  --var-background: #fff;
  --var-text: #000;
  --accent-background: #383838;
  --accent: #9bb537;
  --grey: #b4b4b4;
  --var-dark: #050505;
  --var-grey: rgba(5, 5, 5, .5);
  --var-grey-menu: rgba(5, 5, 5, .3);
}

:root[data-theme="dark"] {
  --var-background: #1e1e1e;
  --var-text: #fff;
  --var-dark: #fff;
  --var-grey: rgba(255, 255, 255, .5);
  --var-grey-menu: rgba(255, 255, 255, .3);
}

body.show-modal-order-now {
  overflow: hidden;
}

body.show-modal-order-now .order-now-modal {
  transform: scale(1);
}

body.show-modal-order-now .order-now-backdrop {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

.order-now-backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 100;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(46, 47, 66, .4);
  justify-content: center;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 30px;
  transition: opacity .2s ease-in-out, visibility .2s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.order-now-modal {
  max-height: calc(100% - 60px);
  max-width: 335px;
  opacity: 1;
  background-color: #fff;
  border-radius: 15px;
  flex-direction: column;
  padding: 28px 20px;
  transition: transform .2s ease-in-out;
  display: flex;
  position: relative;
  transform: scale(1.1);
}

.order-now-modal-title {
  text-transform: uppercase;
  margin: 0 0 40px;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.2;
}

.order-now-modal-button-close-js {
  cursor: pointer;
  background: none;
  border: none;
  position: absolute;
  top: 14px;
  right: 14px;
}

.order-now-form-wrap {
  overflow-y: auto;
}

.order-now-form-wrap::-webkit-scrollbar {
  width: 0;
}

.order-now-modal-form {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.order-now-modal-form label:not(:last-of-type) {
  margin-bottom: 16px;
}

.order-now-modal-form span {
  color: rgba(5, 5, 5, .5);
  margin-bottom: 8px;
  font-size: 14px;
  line-height: 1.2;
  display: block;
}

.order-now-modal-form__input {
  height: 43px;
  padding-left: 10px;
  padding-right: 10px;
}

.order-now-modal-form input, textarea {
  width: 295px;
  border: 1px solid rgba(5, 5, 5, .2);
  border-radius: 15px;
}

.order-now-modal-form__input:focus, .order-now-modal-form textarea:focus {
  outline-color: #9bb537;
}

.order-now-modal-form textarea {
  height: 100px;
  resize: none;
  margin-bottom: 40px;
  padding: 15px;
  overflow: scroll;
}

.order-now-modal-form button {
  width: 100%;
  color: #fff;
  cursor: pointer;
  background: #9bb537;
  border: none;
  border-radius: 15px;
  padding: 15px 0;
  font-size: 14px;
}

.order-now-modal-close-svg path {
  stroke: #050505;
  transition: stroke var(--animation-set);
}

.order-now-modal-close-svg:hover path {
  stroke: #9bb537;
}

@media screen and (max-width: 320px) {
  .order-now-modal {
    width: calc(100% - 30px);
  }

  .order-now-modal-form input, textarea {
    width: 100%;
  }
}

@media screen and (min-width: 767px) {
  .order-now-modal {
    max-width: 440px;
    padding: 40px;
  }

  .order-now-modal-form input, textarea {
    width: 360px;
    border: 1px solid rgba(5, 5, 5, .2);
    border-radius: 15px;
  }

  .order-now-modal-form__input {
    height: 48px;
  }

  .order-now-modal-close-svg {
    width: 24px;
    height: 24px;
  }
}



